body {
    background-color: #CE1141;
}

.Main-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(241, 239, 239);
    width: 800px;
    height: 550px;
    margin: auto;
    margin-top: 30px;
    border-radius: 10px;
   
}

.test {
    min-width: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 800px;
   
    gap: 20px;
}


#Toronto-raptors {
    background-color: grey;
    height: 0px;
}

.Raptors-logo {
    width: 50px;
    height:50px;
    
}

#Raptors-logo {
    object-fit: scale-down;
    width: 100%;
    height: 100%;
    margin-top: 15px;
}

#logo {
    object-fit: scale-down;
    width: 100%;
    height: 100%;
    margin-top: 15px;
}
h1 {
    margin-top: 50px;
    font-family: helvetica;
}

.Record-div {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100px;

}

p {
    font-size: 24px;
}

.Score {
    width: 40px;
   
    margin-left: 40px;
    text-align: end;
}

.Score2 {
    width: 40px;
    margin-right: 40px;
   
}

#record {
    margin-top: 20px;
    font-size: 12px;
    color: rgb(114, 111, 111);
}

.Name {
    
    margin-top: 25px;
    font-size: 16px;
    color: rgb(0, 0, 0);
    width: 120px;
}


.Name2 {
    margin-top: 25px;
    font-size: 16px;
    color: rgb(0, 0, 0);
    width: 120px;
    text-align:end;
}

.Leaders {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 600px;
}

.Leaders2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 200px;
}

.Leaders3 {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
   
    width: 250px;

}

.stats {
    font-size: 12px;
    margin-top: 35px;
    color: rgb(114, 111, 111);
}

.points {
    font-size: 12px;
    font-weight: 600;
    margin-top: 35px;
    margin-right: 5px;
}

.Player-Name {
    font-size: 12px;
    margin-top: 25px;
    width: 60px;
    height: 50px;
    
}

.headShot {
    width: 100px;
    height: 73px;
}

#Stats-Leader {
    font-size: 16px;
    text-align: center;
}